@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "OpenSans";
  font-display: swap;
  src: url("../../fonts/OpenSans-Light.woff2") format("woff2"), url("../../fonts/OpenSans-Light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "OpenSans";
  font-display: swap;
  src: url("../../fonts/OpenSans-Regular.woff2") format("woff2"), url("../../fonts/OpenSans-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "OpenSans";
  font-display: swap;
  src: url("../../fonts/OpenSans-SemiBold.woff2") format("woff2"), url("../../fonts/OpenSans-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "OpenSans";
  font-display: swap;
  src: url("../../fonts/OpenSans-Bold.woff2") format("woff2"), url("../../fonts/OpenSans-Bold.woff") format("woff");
}
html {
  box-sizing: border-box;
  height: 100%;
  font-weight: 400;
  font-family: "OpenSans", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-style: normal;
  font-size: 16px;
  line-height: 32px;
}

.body--locked {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 100px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 768px) {
  .container {
    padding: 0 15px;
  }
}

.page-header {
  width: 100%;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 300;
  letter-spacing: -0.01em;
  position: absolute;
  z-index: 2;
}

.page-header--nav-open {
  background-color: #ffffff;
  color: #48494d;
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  position: fixed;
  height: 100vh;
}
.page-header--nav-open .page-header__logo {
  display: none;
}
.page-header--nav-open .page-header__site-nav {
  width: 100%;
}
.page-header--nav-open .site-nav__list {
  display: flex;
}
.page-header--nav-open .site-nav__toggle {
  background-image: url("../img/svg/icon-menu-cross.svg");
  height: 22px;
  position: absolute;
  right: 0;
  background-color: transparent;
}

.page-header__container {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1073px;
}
@media (max-width: 1023px) {
  .page-header__container {
    align-items: flex-end;
    justify-content: space-between;
    padding: 0;
  }
}

.page-header__logo {
  display: none;
}
@media (max-width: 1023px) {
  .page-header__logo {
    display: inline-block;
    margin-left: 21px;
  }
}

.page-header__site-nav {
  width: 100%;
}
@media (max-width: 1023px) {
  .page-header__site-nav {
    width: auto;
  }
}

@media (max-width: 1023px) {
  .page-header--nav-nojs {
    background-color: #ffffff;
    color: #48494d;
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    position: relative;
  }
  .page-header--nav-nojs .page-header__logo {
    display: none;
  }
  .page-header--nav-nojs .page-header__site-nav {
    width: 100%;
  }
  .page-header--nav-nojs .site-nav__list {
    display: flex;
  }
  .page-header--nav-nojs .site-nav__toggle {
    background-image: url("../img/svg/icon-menu-cross.svg");
    height: 22px;
    position: absolute;
    right: 0;
    background-color: transparent;
    display: none;
  }
}
.site-nav {
  display: flex;
  justify-content: center;
  position: relative;
}
.site-nav::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.2);
  bottom: 0;
}
@media (max-width: 1023px) {
  .site-nav::after {
    display: none;
  }
}

.site-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 1023px) {
  .site-nav__list {
    display: none;
    align-items: flex-start;
    width: 100%;
    min-height: 330px;
    margin-top: 100px;
    flex-direction: column;
    flex-wrap: nowrap;
  }
}

.site-nav__item a {
  text-decoration: none;
  display: flex;
  padding: 16px;
}
@media (max-width: 1023px) {
  .site-nav__item {
    width: 100%;
    background-color: #f6f6f6;
    background-image: url("../img/svg/icon-arrow.svg");
    background-repeat: no-repeat;
    background-position: right 24px center;
  }
}

.site-nav__toggle {
  background-image: url("../img/svg/icon-menu-burger.svg");
  background-repeat: no-repeat;
  background-position: center;
  width: 22px;
  height: 13px;
  border: 0;
  outline: 0;
  background-color: #0ad9c6;
  padding: 25px;
  display: none;
}
@media (max-width: 1023px) {
  .site-nav__toggle {
    display: flex;
  }
}

